@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
* {
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
}

a {
  text-decoration: none;
  color: lightgray;
}

body {
  font-family: "Rubik", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width: 1100px;
  margin: 0 auto;
  background-color: #282c34;
  color: white;
}

.App {
  text-align: left;
  padding: 20px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.won-card {
  background-color: #4da375;
}

.lost-card {
  background-color: #a34d5d;
}

.match-detail-card{display:grid;grid-template-columns:1fr 1fr;grid-template-rows:1fr;margin-top:10px;margin-bottom:15px;padding:8px}.match-detail-card .vs,.match-detail-card .on,.match-detail-card .at,.match-detail-card .match-winner{margin-top:10px}.match-detail-card .match-winner{margin-top:40px}.match-detail-card .other-details{text-align:right}.match-detail-card .other-details>p{margin-bottom:20px}span{display:inline-block}
.match-small-card{padding:10px;margin-bottom:20px}.match-small-card .match-result{margin-top:15px}.match-small-card .vs{margin-bottom:10px}
.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:auto;grid-column-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:center}.TeamPage .team-name{font-size:5rem}.TeamPage .match-detail-card-section{grid-column:span 4}.TeamPage .more{align-self:center;justify-self:center}.TeamPage .win-loss-section{justify-self:center;text-align:center}.TeamPage .more{font-size:1.5rem}
.app-name{text-align:center;font-size:3rem;margin:30px auto}
.year-selector{text-align:right;list-style-type:none;padding:20px}.year-selector li{margin-top:10px;margin-bottom:10px;list-style-type:none}
.match-page{display:grid;grid-template-columns:1fr 5fr;grid-template-rows:1fr;grid-column-gap:20px}.match-page .year{justify-self:end}.match-page .team-year{margin-top:20px;margin-bottom:10px;text-align:center}
.team-single-tile{text-align:center;height:170px;border:1px solid #fff;padding:20px}.team-name{justify-content:center;align-items:center}
.home-page .team{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}
